<template>
<MyDialog title="Открыть аккаунт по ID или Email">
  <template v-slot:activator="{ on, attrs }">
    <v-list-item v-bind="attrs" v-on="on">
      <v-list-item-title>Аккаунт</v-list-item-title>
    </v-list-item>
  </template>
  <v-card-text>
    <v-text-field autofocus @keyup.enter.prevent="open" v-model="id" label="ID"/>
  </v-card-text>
  <v-card-actions>
    <v-btn @click="open" :loading="loading" color="primary">Перейти</v-btn>
  </v-card-actions>
</MyDialog>
</template>

<script>
import MyDialog from '@/render/components/Common/MyDialog';

export default {
  components: { MyDialog },
  data: () => ({
    id: '',
    loading: false
  }),

  methods: {
    async open() {
      if (Number.isNaN(parseInt(this.id, 10))) {
        this.loading = true;
        const { data } = await this.api.findFiltered('account', { account: this.id });
        this.loading = false;

        if (!data[0]) {
          alert(`Аккаунт ${this.id} не найден.`);
          return;
        }

        this.tabMgr.open('Account', { elId: data[0].id });
        return;
      }

      this.tabMgr.open('Account', { elId: parseInt(this.id, 10) });
    }
  }
};
</script>
